import { CrownTwoTone, PoweroffOutlined } from '@ant-design/icons';

const ChatTop = ({creds}) => {
  if(!creds) return ""

  const handleLogout = (e) => {
    localStorage.removeItem("username");
    localStorage.removeItem("password");
    window.location.reload();
  }

  return (
    <div className="chat-top">
      <p><CrownTwoTone />{` Welcome ${creds.userName}`}</p>
      <button type="button" className="send-off" title="Logout" onClick={handleLogout}>
        <PoweroffOutlined /> Logout
      </button>
    </div>
  )
}
export default ChatTop;